* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Quicksand", sans-serif;
}

h1 {
  font-weight: 900;
  font-size: 48px;
}

footer {
  background-color: #2f4f4f;
  background-color: var(--green);
  color: #fff;
  height: 50px;
  width: 100%;
  text-align: center;
  padding-top: 12px;
  position: fixed;
  bottom: 0;
  left: 0;
  font-size: 1.2rem;
}

footer a {
  color: #A26262;
  color: var(--purple);
  font-weight: 800;
}

footer a:hover {
  color: #fff;
}

/* Colours */
:root {
  --purple: #A26262;
  --green: #2f4f4f;
}

/* Shared Classes */

.\--btn {
  margin: 5px;
  padding: 0.5rem 1rem;
  font-size: 18px;
  font-weight: 800;
  border: 1px solid transparent;
  border-radius: 5px;
  background-color: #A26262;
  background-color: var(--purple);
  color: #fff;
  cursor: pointer;
  transition-property: background-color border;
  transition-duration: 1s;
}

.\--btn:hover {
  background-color: #fff;
  color: #A26262;
  color: var(--purple);
  border: 1px solid #A26262;
  border: 1px solid var(--purple);
}


header {
    background-color: var(--green);
    min-height: 100vh;
    text-align: center;
    color: #fff;
    padding-top: 40vh;
}


header span {
    color: var(--purple)
}

header h2 {
    margin-bottom: 2rem;
}
.icons {
    margin: 1rem;
    display: flex;
    justify-content: space-between;
}

.about {
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    width: 80%;
    justify-content: center;
    min-height: 60vh;
}

.about img {
    margin: auto 1rem;
    border-radius: 50%;
    border: 5px solid var(--green);
    width: 300px;
    height: 300px;
}

.about-text {
    margin: auto 0;
    max-width: 450px;
    color: #6e6e6e;
}

.about-text h2 {
    color: var(--purple);
    font-size: 62px;
}
.project-card {
    width: 350px;
    max-width: 400px;
    height: 160px;
    background-color: var(--green);
    color: #fff;
    margin: 1rem;
    overflow: hidden;
    cursor: pointer;
    border: 2px solid #ccc;
    border-radius: 3px;
}

.project-card:hover {
    border: 2px solid var(--purple);
    background-color: rgba(0, 0, 0, 0.8);
}

.project-popup {
    padding: 1rem;
    width: 80vw;
    min-height: 80vh;
    background-color: rgba(0, 0, 0, 0.9);
    border: 3px solid var(--green);
    border-radius: 5px;
    color: #fff;
    position: absolute;
    top: 5vh;
    left: 10vw;
    -webkit-animation: growIn 0.2s linear;
            animation: growIn 0.2s linear;
}

@-webkit-keyframes growIn {
    0% {
        scale: 0;
    }
    100% {
        scale: 1;
    }
}

@keyframes growIn {
    0% {
        scale: 0;
    }
    100% {
        scale: 1;
    }
}

.project-popup h3 {
    padding: 1rem;
}

.project-popup img {
    max-height: 50vh;
    max-width: 100%;
    border-radius: 3px;
}

.project-popup span {
    color: var(--purple);
    font-weight: 900;
}

.project-popup button {
    margin: 15px;
}

.card-overlay {
    background-color: transparent;
}

.card-overlay:hover {
    
    background-color: rgba(0, 0, 0, 0.8);
}
.projects {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 4rem;
    padding-top: 2rem;
}

.project-list h2 {
    font-size: 62px;
    color: var(--purple);
}

.project-list {
    
    color: #6e6e6e;
}
.page-window {
    max-width: 80%;
    text-align: center;
    margin: 0 auto;
    -webkit-animation: slideIn 0.3s linear;
            animation: slideIn 0.3s linear;
}

@-webkit-keyframes slideIn {
  from {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
   }
    
    to {
        visibility: hidden;
        -webkit-transform: translateY(0);
                transform: translateY(0);
  }
}

@keyframes slideIn {
  from {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
   }
    
    to {
        visibility: hidden;
        -webkit-transform: translateY(0);
                transform: translateY(0);
  }
}

hr {
    border: 1px solid var(--purple);
}

.btn-to-top {
    position: fixed;
    right: 0;
    bottom: 4rem;
    padding: 10px;
    margin: 3px;
    background-color: var(--purple);
    border: 2px solid transparent;
    border-radius: 5px;
    color: #fff;
    cursor: pointer;
    transition-property: background-color border;
    transition-duration: 1s;
}

.btn-to-top:hover {
    background-color: #fff;
    color: var(--purple);
    border: 2px solid var(--purple);
}

.project-section {
    padding-top: 3rem;
    min-height: 80vh;
}

.contact {
    margin: 3rem;
    padding-bottom: 4rem;
  }
  
  .contact h2 {
    font-size: 62px;
    color: var(--purple);
  }

  .contact span {
    display: flex;
    justify-content: center;
    padding: 5px;
  }

  .contact h4 {
    margin: 0 15px;
    color: #6e6e6e;
  }
.see-more-btn {
    margin: 3rem;
}
